<template>
    <section>
        <div class="my-popups">
            <loader :is-visible="isLoading" />

            <div class="modal d-block">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div
                            class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
                            <h5 class="modal-title">Recharge Pay</h5>
                            <button type="button" class="close" @click="CloseClientWalletRecharge()">
                                <em class="fa fa-times" aria-hidden="true"></em>
                            </button>
                        </div>

                        <div class="modal-body">

                            <div class="container-fluid invoice-payment p-0">
                                <!-- Company Selection -->
                                <loader :is-visible="isLoading"></loader>


                                <div class="col-sm-12 col-md-12 float-left col-lg-12 col-xl-12 p-0">


                                    <!-- Payment Section -->
                                    <div class="card probx mt-0 recharge_payment">
                                        <div class="card-header">
                                            <h2 class="mb-0">{{ $t('PaymentGateway') }}</h2>
                                        </div>
                                        <div class="card-body">

                                            <div id="ProcessPayment">
                                                <!-- <div class="form-group">
                                                    <label class="font-weight-bold">{{ $t('PaymentGateway') }}: </label>
                                                </div> -->



                                                <div class="clientwalletsprite">

                                                    <div v-for="(item, index) in paymentGateways" :key="index"
                                                        class="col-sm-12 col-lg-12 col-md-12 mb-0 p-0"
                                                        v-if="item.PaymantGatewayType !== 'Wallet'">
                                                        <button type="button" class="btn w-100 py-3"
                                                            :class="[item.PaymantGatewayType, '', { active: selectedGateway === item.PaymentGatewayCode }]"
                                                            @click="selectGateway(item.PaymentGatewayCode)">
                                                            <em
                                                                :class="selectedGateway === item.PaymentGatewayCode ? 'fa fa-check-circle float-left font-20 text-success' : 'fa fa-circle-thin float-left font-20'"></em>
                                                            <!-- {{ item.PaymantGatewayType }} -->
                                                        </button>
                                                    </div>

                                                    <div v-if="PaymentLink" class="col-md-4 mb-3">
                                                        <a :href="PaymentLink" target="_blank"
                                                            class="btn btn-outline-info w-100 py-3">
                                                            {{ $t('ExternalLink') || 'External Link' }}
                                                        </a>
                                                    </div>
                                                </div>
                                                <input type="hidden" name="PaymentGatewayCode"
                                                    :value="selectedGateway" />


                                                <div class="right-forms">
                                                    <div v-if="selectedGateway == 'Stripe'">
                                                        <form ref="paymentForm" @submit.prevent="submitPayment">

                                                            <div class="row py-3">

                                                                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                    <div class="form-group">
                                                                        <label for="cardholder-name">Name On
                                                                            Card:</label>
                                                                        <input v-model="billingDetails.name" type="text"
                                                                            placeholder="Name On Card"
                                                                            class="stripe-input w-100  form-control" />
                                                                    </div>
                                                                </div>

                                                                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                    <div class="form-group">
                                                                        <label>Card Number: <span
                                                                                class="mandatory">*</span></label>
                                                                        <div id="card-number-element"
                                                                            class="stripe-input form-control"></div>
                                                                    </div>
                                                                </div>

                                                                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                    <div class="form-group">
                                                                        <label>Expiry Date: <span
                                                                                class="mandatory">*</span></label>
                                                                        <div id="card-expiry-element"
                                                                            class="stripe-input form-control"></div>
                                                                    </div>
                                                                </div>

                                                                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                    <div class="form-group">
                                                                        <label>CVC: <span
                                                                                class="mandatory">*</span></label>
                                                                        <div id="card-cvc-element"
                                                                            class="stripe-input form-control"></div>
                                                                    </div>
                                                                </div>
                                                                <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                    <div class="form-group">
                                                                        <label>Recharge Amount ({{ BaseCurrencyCode
                                                                            }}):</label>
                                                                        <input type="text" class="form-control font-weight-bold"
                                                                            :value="RechargeAmount ? parseFloat(RechargeAmount).toFixed(2) : '0.00'"
                                                                            disabled />
                                                                    </div>
                                                                </div>

                                                                <div id="card-errors" class="stripe-error">
                                                                    {{ errorMessage }}</div>

                                                            </div>

                                                            <!-- <div class="cont_99P">
                                                                            <span class="mandatory pr font12">Fields
                                                                                marked with * are mandatory.</span>
                                                                        </div> -->



                                                            <!-- <button type="submit" class="btn btn-primary">Submit</button>
                                                    <div class="text-right">
                                                        <a href="javascript:;" class="btn btn-secondary"
                                                            @click="RechargeCancel()">
                                                            <em class="fa fa-times pr-2"></em>{{ $t('Cancel') }}
                                                        </a>
                                                    </div> -->


                                                            <div class="col-12 mt-3 p-0">
                                                                <div class="row flex-row-reverse">
                                                                    <div class="col-lg-6 text-right">
                                                                        <!-- <button type="submit" class="btn btn-success">Submit</button>  -->
                                                                        <!-- Hidden fields to be submitted -->
                                                                        <input type="hidden" ref="stripeToken">
                                                                        <input type="hidden" ref="hdnCardType">
                                                                        <input type="hidden" ref="hdnNameonCard">
                                                                        <input type="hidden" ref="hdnCardNumber">
                                                                        <input type="hidden" ref="hdnCardMonth">
                                                                        <input type="hidden" ref="hdnCardYear">
                                                                        <input type="hidden" ref="hdnCvv">

                                                                        <a href="javascript:;" class="btn btn-info mr-1"
                                                                            @click="submitPayment()">
                                                                            <em
                                                                                class="fa fa-credit-card-alt pr-2"></em>{{
                                                                                    $t('PayNow') }}
                                                                        </a>
                                                                        <a href="javascript:;" class="btn btn-danger"
                                                                            @click="RechargeCancel()">
                                                                            <em class="fa fa-times pr-2"></em>{{
                                                                                $t('Cancel') }}
                                                                        </a>
                                                                    </div>
                                                                    <div class="col-lg-6">
                                                                        <div class="py-2 mandatory">Fields marked with *
                                                                            are
                                                                            mandatory.</div>
                                                                    </div>
                                                                </div>
                                                            </div>

                                                        </form>
                                                    </div>

                                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0"
                                                        v-else-if="selectedGateway == 'PayU' || selectedGateway == 'Razorpay'">

                                                        <div class="row py-3">
                                                            <div class="col-sm-12 col-md-4 col-lg-4 col-xl-4 ">
                                                                <div class="form-group">
                                                                    <label>Recharge Amount ({{ BaseCurrencyCode
                                                                        }}):</label>
                                                                    <input type="text" class="form-control font-weight-bold"
                                                                        :value="RechargeAmount ? parseFloat(RechargeAmount).toFixed(2) : '0.00'"
                                                                        disabled />
                                                                </div>
                                                            </div>
                                                            <div class="col-12">
                                                                <div class="row flex-row-reverse">

                                                                    <div class="col-lg-6 text-right">
                                                                        <a href="javascript:;" class="btn btn-info mr-1"
                                                                            @click="RechargePayNow"><em
                                                                                class="fa fa-credit-card-alt pr-2"></em>{{
                                                                                    $t('PayNow') }}</a>
                                                                        <a href="javascript:;" class="btn btn-secondary"
                                                                            @click="RechargeCancel()">
                                                                            <em class="fa fa-times pr-2"></em>{{
                                                                                $t('Cancel') }}
                                                                        </a>
                                                                    </div>

                                                                    <div class="col-lg-6">
                                                                        <div class="py-2 mandatory">{{
                                                                            $t('MandatoryString')
                                                                            }}</div>
                                                                    </div>

                                                                </div>
                                                            </div>
                                                        </div>

                                                    </div>

                                                    <div v-else>

                                                        <div class="row mb-3 py-3">
                                                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                <div class="form-group">
                                                                    <label>{{ $t('CardType') }}:<span
                                                                            class="text-danger">*</span></label>
                                                                    <select v-model="card.cardType" class="form-control"
                                                                        :class="{ 'is-invalid': cardErrors.cardType }"
                                                                        @change="cardErrors.cardType = ''">
                                                                        <option value="Visa">Visa</option>
                                                                        <option value="Master">Master Card</option>
                                                                        <option value="Discover">Discover</option>
                                                                        <option value="Amex">American Express</option>
                                                                    </select>
                                                                    <small class="text-danger"
                                                                        v-if="cardErrors.cardType">{{
                                                                            cardErrors.cardType }}</small>
                                                                </div>
                                                            </div>

                                                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                <div class="form-group">
                                                                    <label>{{ $t('CardNumber') }}:<span
                                                                            class="text-danger">*</span></label>
                                                                    <input type="text" v-model="card.number"
                                                                        class="form-control"
                                                                        placeholder="1234 1234 1234 1234"
                                                                        :class="{ 'is-invalid': cardErrors.number }"
                                                                        @input="cardErrors.number = ''" />
                                                                    <small class="text-danger"
                                                                        v-if="cardErrors.number">{{
                                                                            cardErrors.number }}</small>
                                                                </div>
                                                            </div>

                                                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                <div class="form-group">
                                                                    <label>{{ $t('NameOnCard') }}:<span
                                                                            class="text-danger">*</span></label>
                                                                    <input type="text" v-model="card.name"
                                                                        class="form-control"
                                                                        :class="{ 'is-invalid': cardErrors.name }"
                                                                        @input="cardErrors.name = ''" />
                                                                    <small class="text-danger" v-if="cardErrors.name">{{
                                                                        cardErrors.name }}</small>
                                                                </div>
                                                            </div>

                                                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                <div class="form-group">
                                                                    <label>{{ $t('ExpirationDate') }}:<span
                                                                            class="text-danger">*</span></label>
                                                                    <div class="row no-gutters">
                                                                        <div class="col-6 pr-1">
                                                                            <select v-model="card.expMonth"
                                                                                class="form-control"
                                                                                :class="{ 'is-invalid': cardErrors.expMonth }"
                                                                                @change="cardErrors.expMonth = ''">
                                                                                <option disabled value="">Month</option>
                                                                                <option v-for="(month, index) in months"
                                                                                    :key="index"
                                                                                    :value="(index + 1).toString().padStart(2, '0')">
                                                                                    {{ month }}
                                                                                </option>
                                                                            </select>
                                                                            <small class="text-danger"
                                                                                v-if="cardErrors.expMonth">{{
                                                                                    cardErrors.expMonth
                                                                                }}</small>
                                                                        </div>
                                                                        <div class="col-6 pl-1">
                                                                            <select v-model="card.expYear"
                                                                                class="form-control"
                                                                                :class="{ 'is-invalid': cardErrors.expYear }"
                                                                                @change="cardErrors.expYear = ''">
                                                                                <option disabled value="">Year</option>
                                                                                <option v-for="y in years" :key="y">{{ y
                                                                                    }}
                                                                                </option>
                                                                            </select>
                                                                            <small class="text-danger"
                                                                                v-if="cardErrors.expYear">{{
                                                                                    cardErrors.expYear
                                                                                }}</small>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>


                                                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4">
                                                                <div class="form-group">
                                                                    <label>Recharge Amount ({{ BaseCurrencyCode
                                                                        }}):</label>
                                                                    <input type="text" class="form-control font-weight-bold"
                                                                        :value="RechargeAmount ? parseFloat(RechargeAmount).toFixed(2) : '0.00'"
                                                                        disabled />
                                                                </div>
                                                            </div>

                                                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-3 d-none">
                                                                <div class="form-group">
                                                                    <label class="d-block mb-2">{{ $t('PaymentType')
                                                                        }}:</label>
                                                                    <div class="d-flex align-items-center">
                                                                        <div class="form-check mr-3">
                                                                            <input type="radio" id="full" value="f"
                                                                                v-model="paymentType"
                                                                                class="form-check-input" />
                                                                            <label class="form-check-label" for="full">
                                                                                {{ $t('FullBalance') }}
                                                                            </label>
                                                                        </div>
                                                                        <div class="form-check">
                                                                            <input type="radio" id="partial" value="p"
                                                                                v-model="paymentType"
                                                                                class="form-check-input" />
                                                                            <label class="form-check-label"
                                                                                for="partial">
                                                                                {{ $t('PartialPaid') }}
                                                                            </label>
                                                                        </div>
                                                                    </div>
                                                                </div>
                                                            </div>

                                                            <div class="col-sm-12 col-md-6 col-lg-4 col-xl-4 d-none">
                                                                <div class="form-group">
                                                                    <label>Enter Amount :</label>
                                                                    <input type="text" class="form-control"
                                                                        v-model="paymentAmount" />
                                                                </div>
                                                            </div>
                                                        </div>

                                                        <!-- <div class="col-12 p-0">
                                                    <div class="row flex-row-reverse">
                                                        <div class="text-right">
                                                            
                                                        </div>

                                                        <div class="text-right">
                                                            
                                                        </div>
                                                        <div class="col-lg-6 text-danger">{{ $t('MandatoryString') }}
                                                        </div>
                                                    </div>
                                                </div> -->

                                                        <div class="col-12 mt-3 p-0">
                                                            <div class="row flex-row-reverse ">
                                                                <div class="col-lg-6 text-right">
                                                                    <!-- <a href="javascript:;" id="aChainTicketSave" class="btn btn-primary save-btn mr-2" title="@Resources.Save"><i class="fa fa-floppy-o pr-2"></i><span>@Resources.Save</span></a>
                                                         <a href="@Url.Action("Chain", "Ticketing")" title="@Resources.Cancel" id="gobacktolistscreen" class="btn btn-primary delete-btn" value="@Model.TicketChainId"><i class="fa fa-times pr-2"></i><span>@Resources.Cancel</span></a>
                                                         -->
                                                                    <a href="javascript:;" class="btn btn-info mr-1"
                                                                        @click="RechargePayNow">
                                                                        <em class="fa fa-credit-card-alt pr-2"></em>{{
                                                                            $t('PayNow') }}
                                                                    </a>
                                                                    <a href="javascript:;" class="btn btn-danger"
                                                                        @click="RechargeCancel()">
                                                                        <em class="fa fa-times pr-2"></em>{{
                                                                            $t('Cancel') }}
                                                                    </a>

                                                                </div>
                                                                <div class="col-lg-6">
                                                                    <div class="py-2 mandatory">{{ $t('MandatoryString')
                                                                        }}
                                                                    </div>
                                                                </div>
                                                            </div>
                                                        </div>

                                                    </div>



                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <ClientWalletRechargeReceipt v-if="IsRechargeReceiptShow" :TransactionId="TransactionId">
        </ClientWalletRechargeReceipt>
    </section>
</template>

<script>

import DataService from "../../services/DataService";
import ClientWalletRechargeReceipt from "./ClientWalletRechargeReceipt.vue";
export default {
    components: {
        ClientWalletRechargeReceipt
    },
    props: {
        RechargeAmount: {
            type: Number,
            required: true,
        },
        BaseCurrencyCode: {
            type: String,
            required: false,
        },
    },
    watch: {
        async selectedGateway(newVal) {
            
            if (newVal !== 'Stripe') return;

            await this.loadStripeScript();
            this.initializeStripe();
        }
    },
    async mounted() {
        
        await this.createdDone;
        //if (!this.isStripePaymentGateway) return;

        if (this.selectedGateway !== 'Stripe') return;

        // Load Stripe script before initializing
        await this.loadStripeScript();

        this.initializeStripe();
    },

    data() {
        return {
            isLoading: false,
            IsUtilityBilling: false,
            paymentHistoryData: [],
            paymentList: [],
            MainInvocieNetAmount: 0,
            totalPayments: 0,
            templatedata: [],
            companies: [],
            selectedCompanyId: "",
            paymentGateways: [],
            selectedGateway: "",
            PaymentLink: "",
            paymentAmount: "",
            paymentType: "f",
            card: {
                cardType: "Visa",
                number: "",
                name: "",
                expMonth: "",
                expYear: "",
            },
            years: [],
            months: [
                "January", "February", "March", "April", "May", "June",
                "July", "August", "September", "October", "November", "December"
            ],
            UserId: "",
            BillIdEnc: "",
            BillId: '',
            cardErrors: {
                cardType: "",
                number: "",
                name: "",
                expMonth: "",
                expYear: "",
            },
            CompanyName: null,
            JsonResponse: null,
            TransactionId: null,
            IsRechargeReceiptShow: false,
            UniqueId: null,
            ClientEmail: null,
            ClientName: null,

            stripe: null,
            elements: null,
            cardNumber: null,
            cardExpiry: null,
            cardCvc: null,
            errorMessage: "",
            billingDetails: {
                name: ""
            },

            isStripePaymentGateway: true,
            stripePublishableKey:
                "pk_test_51RjfwB4C6gUvp1ei45F8RMBEUh9vCrFMZgb6Qgrs78Rs9ED9gX2H0HpVm0Pv7NlHiGOc7wdJ1OtH7phwoxpJMgMY00tzECaTbs",
            createdDone: null,

        };
    },
    // computed: {
    //     noGateways() {
    //         return (!this.paymentGateways || this.paymentGateways.length === 0) && !this.PaymentLink;
    //     },

    // },

    created: async function () {
        this.createdDone = (async () => {
            
            var vm = this;
            await vm.AddPaymentTranaction();
            vm.UserId = vm.GetUserInfo.ID;
            vm.CompanyName = vm.GetUserInfo.CompanyName;
            vm.ClientEmail = vm.GetUserInfo.Email;
            vm.ClientName = vm.GetUserInfo.Name;
            vm.generateYears();
            await vm.GetActiveGatewayDetail();
        })();
    },
    methods: {

        CloseClientWalletRecharge() {

            this.$parent.IsClientWalletRechargePayNow = false;
        },

        async AddPaymentTranaction() {
            
            var vm = this;
            const url = `Amount=${vm.RechargeAmount}&Description=WALLET_RECHARGE`;
            await DataService.AddPaymentTranaction(url).then((response) => {

                if (response.data) {

                    vm.UniqueId = response.data[0].UniqueId;
                    vm.TransactionId = response.data[0].TransactionId;
                }
                else {
                    vm.UniqueId = null;
                    vm.TransactionId = null;
                }

            });
        },

        generateYears() {

            const currentYear = new Date().getFullYear();
            for (let i = 0; i < 15; i++) {
                this.years.push(currentYear + i);
            }
        },

        async GetActiveGatewayDetail() {
            
            try {
                var vm = this;
                vm.isLoading = true;
                const response = await DataService.GetActiveGatewayDetail();
                if (response.data?.result) {
                    const parsedGateways = JSON.parse(response.data.result);
                    parsedGateways.forEach(item => {
                        if (item.parameters) {
                            try {
                                item.parameters = JSON.parse(item.parameters);
                            } catch (e) {
                                console.warn("Invalid parameters JSON:", item.parameters);
                                item.parameters = {};
                            }
                        }
                    });
                    vm.paymentGateways = parsedGateways;

                    // ? Automatically select the first gateway if available
                    if (vm.paymentGateways.length > 0) {
                        vm.selectedGateway = vm.paymentGateways[0].PaymentGatewayCode;
                        vm.isLoading = false;
                    }

                } else {
                    vm.paymentGateways = [];
                    vm.isLoading = false;
                }
            } catch (err) {
                console.error("Error loading gateways:", err);
                vm.paymentGateways = [];
                vm.isLoading = false;
            }
        },
        selectGateway(code) {
            
            this.selectedGateway = code;
        },

      RechargePayNow() {
    var vm = this;

    // Start loader immediately
    vm.isLoading = true;

    if (vm.selectedGateway == 'Authorize') {

        let valid = true;

        if (!vm.card.cardType) {
            vm.cardErrors.cardType = "Card type is required.";
            valid = false;
        }
        if (!vm.card.number) {
            vm.cardErrors.number = "Card number is required.";
            valid = false;
        } else if (!/^\d{13,19}$/.test(vm.card.number)) {
            vm.cardErrors.number = "Invalid card number.";
            valid = false;
        }
        if (!vm.card.name) {
            vm.cardErrors.name = "Name on card is required.";
            valid = false;
        }
        if (!vm.card.expMonth) {
            vm.cardErrors.expMonth = "Expiration month is required.";
            valid = false;
        }
        if (!vm.card.expYear) {
            vm.cardErrors.expYear = "Expiration year is required.";
            valid = false;
        }

        if (!valid) {
            vm.isLoading = false; // Stop loader if validation fails
            return;
        }

        var Obj = {
            CardMonth: vm.card.expMonth,
            CardNum: vm.card.number,
            CardType: vm.card.cardType,
            CardYear: vm.card.expYear,
            CompanyName: vm.CompanyName,
            CustomerId: '',
            InvoiceId: 0,
            NameOnCard: vm.card.name,
            NetAmount: vm.RechargeAmount,
            PaidAmount: vm.RechargeAmount,
            PaymentGatewayCode: vm.selectedGateway,
            RefId: '',
            BillIdEnc: 0,
            Description: 'WALLET_RECHARGE',
            StripeToken: null,
            UniqueId: vm.UniqueId,
            TransactionId: vm.TransactionId,
            ClientEmail: vm.ClientEmail
        };
        var JsonObj = JSON.stringify(Obj);

        DataService.MakeRealPartialpayment(JsonObj)
            .then(function (response) {
                vm.TransactionId = response.data.data;

                if (response.data.status == 1) {
                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Payment")]), "success", true, vm.$t("Alert"));
                } else if (response.data.status == 2) {
                    vm.ShowAlert("Payment not completed", "failure", true, vm.$t('Alert'));
                } else {
                    vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                }

                vm.IsRechargeReceiptShow = true;
            })
            .catch((error) => {
                console.error(error);
                vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
            })
            .finally(() => {
                vm.isLoading = false; // Stop loader in finally
            });

    } else if (vm.selectedGateway === 'Razorpay') {

        var razorpayObj = {
            amount: vm.RechargeAmount?.toString() || "0",
            currency: "USD",
            name: vm.ClientName || '',
            email: vm.ClientEmail || '',
            transactionId: vm.TransactionId?.toString() || "0",
            notes: "WALLET_RECHARGE",
            invoice_id: 0,
            user_id: vm.GetUserInfo.ID,
            company_id: vm.GetUserInfo.CompanyId,
            reference_no: vm.UniqueId?.toString() || "0"
        };

        vm.isLoading = false; // Stop loader before routing
        vm.$router.push({
            name: "RazorpayCheckout",
            params: { razorpayData: razorpayObj }
        });

    } else { // PayU or other gateways

        var payuObj = {
            txnid: vm.TransactionId?.toString() || "0",
            udf1: vm.UniqueId || "",
            amount: vm.RechargeAmount?.toString() || "0",
            productinfo: "WALLET_RECHARGE",
            firstname: vm.ClientName || '',
            email: vm.ClientEmail || '',
            phonenumber: '',
            udf2: '',
            udf3: vm.selectedGateway
        };

        DataService.MakeRealPartialpaymentPAYU(payuObj)
            .then(function (response) {
                if (response.data && typeof response.data === "string") {
                    window.location.href = response.data;
                    return;
                }
                vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
            })
            .catch((error) => {
                console.error(error);
                vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
            })
            .finally(() => {
                vm.isLoading = false; // Stop loader in finally
            });
    }
},

        async submitPayment() {
            
            var vm = this;
            //if (window.showLoader) showLoader();          

            const { token, error } = await vm.stripe.createToken(vm.cardNumber, {
                name: vm.billingDetails.name
            });

            if (error) {
                //if (window.hideLoader) hideLoader();
                vm.errorMessage = vm.mapStripeError(error);
                return;
            }

            // Fill hidden fields
            vm.$refs.stripeToken.value = token.id;
            vm.$refs.hdnCardType.value = token.card.brand;
            vm.$refs.hdnNameonCard.value = token.card.name;
            vm.$refs.hdnCardNumber.value = token.card.last4;
            vm.$refs.hdnCardMonth.value = token.card.exp_month;
            vm.$refs.hdnCardYear.value = token.card.exp_year;
            vm.$refs.hdnCvv.value = 123;

            var Obj = {
                CardMonth: vm.$refs.hdnCardMonth.value,
                CardNum: vm.$refs.hdnCardNumber.value,
                CardType: vm.$refs.hdnCardType.value,
                CardYear: vm.$refs.hdnCardYear.value,
                CompanyName: 'UtilityBilling',
                CustomerId: '',
                InvoiceId: 0,
                NameOnCard: vm.$refs.hdnNameonCard.value,
                NetAmount: vm.RechargeAmount,
                PaidAmount: vm.RechargeAmount,
                PaymentGatewayCode: 'Stripe',
                RefId: '',
                BillIdEnc: 0,
                Description: 'WALLET_RECHARGE',
                StripeToken: vm.$refs.stripeToken.value,
                UniqueId: vm.UniqueId,
                TransactionId: vm.TransactionId,
                ClientEmail: vm.ClientEmail

            }
            var JsonObj = JSON.stringify(Obj);

            // Submit form
            //this.$refs.paymentForm.submit();

            DataService.MakeRealPartialpayment(JsonObj).then(function (response) {
                vm.isLoading = false;


                if (response.data.status == 1) {

                    vm.ShowAlert(vm.$t("AddedSuccess", [vm.$t("Payment")]), "success", true, vm.$t("Alert"));
                    vm.IsRechargeReceiptShow = true;

                } else if (response.data.status == 2) {

                    vm.IsRechargeReceiptShow = true;


                } else {
                    vm.IsRechargeReceiptShow = true;

                }

            });
        },
        RechargeCancel() {


            //this.$router.push({ name: 'WalletRecharge' });
            this.CloseClientWalletRecharge();
        },
        loadStripeScript() {
            
            return new Promise((resolve, reject) => {
                // If already loaded, resolve immediately
                if (document.querySelector('script[src="https://js.stripe.com/v3/"]')) {
                    resolve();
                    return;
                }

                const script = document.createElement("script");
                script.src = "https://js.stripe.com/v3/";
                script.onload = () => resolve();
                script.onerror = () => reject("Stripe failed to load");
                document.head.appendChild(script);
            });
        },
        initializeStripe() {
            
            // Initialize Stripe and Elements
            this.stripe = Stripe(this.stripePublishableKey);
            this.elements = this.stripe.elements();

            const style = {
                base: {
                    fontSize: "14px",
                    color: "#212529",
                    "::placeholder": { color: "#888" }
                },
                invalid: { color: "#dc3545" }
            };

            // Create elements
            this.cardNumber = this.elements.create("cardNumber", { style });
            this.cardExpiry = this.elements.create("cardExpiry", { style });
            this.cardCvc = this.elements.create("cardCvc", { style });

            // Mount elements
            this.cardNumber.mount("#card-number-element");
            this.cardExpiry.mount("#card-expiry-element");
            this.cardCvc.mount("#card-cvc-element");

            // Handle errors
            [this.cardNumber, this.cardExpiry, this.cardCvc].forEach(el => {
                el.on("change", evt => {
                    this.errorMessage = evt.error ? evt.error.message : "";
                });
            });
        },
        mapStripeError(error) {
            
            const codes = {
                incomplete_number: "Please enter a complete card number.",
                invalid_number: "The card number you entered is invalid.",
                incomplete_cvc: "Please enter your card's CVC.",
                invalid_cvc: "The CVC code you entered is incorrect.",
                incomplete_expiry: "Please enter the full expiration date.",
                invalid_expiry_year_past: "Your card's expiration year is in the past.",
                card_declined: "Your card was declined. Please try another payment method.",
                expired_card: "Your card has expired."
            };

            return codes[error.code] || error.message || "Something went wrong.";
        },

    },
};
</script>

