        </div>
        <transition name="modal">
            <div class="my-popups clientwallet">
                <div class="modal d-block dialog" v-if="isRechargeNow">
                    <div class="modal-dialog modal-lg" style="width: 600px !important;">
                        <div class="modal-content">
                            <div class="modal-header">
                                <h4 class="modal-title">{{ $t('Recharge Wallet') }}</h4>

                                <button type="button" class="close" @click="closeModal">
                                    <em class="fa fa-times" aria-hidden="true"></em>
                                </button>

                            </div>

                            <div class="modal-body recharge_popup">
                                
                                <div class="card recharge mt-0">
                                    <!-- <h3>Recharge Wallet</h3> -->
                                    <label>Enter Amount ({{ BaseCurrencyCode }}):</label>

                                    <input type="number" step="0.01" min="0" placeholder="0.00" v-model="RechargeAmount" @input="onAmountInput($event)" />
                                    <div class="text-danger small mt-1" v-if="RechargeAmount && !isRechargeAmountValid">Please enter a valid amount (up to 10 digits, optional 2 decimals) greater than 0.</div>

                                    <div class="quick-select">
                                        <button v-for="amount in [10, 25, 50, 100]" :key="amount"
                                            @click="setAmount(amount)" :class="{ active: RechargeAmount == amount }">
                                            {{ BaseCurrencyCode }} {{ amount }}
                                        </button>
                                    </div>

                                    <button class="btn btn-success" @click="WalletRecharge"><em class="fa  fa-credit-card"></em> Recharge Now</button>

                                    <div class="note float-left">
                                        <strong>Note:</strong> Funds will be available immediately after successful
                                        payment processing.
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

        <ClientWalletRechargePayNow v-if="IsClientWalletRechargePayNow" :RechargeAmount="RechargeAmount" :BaseCurrencyCode="BaseCurrencyCode">
        </ClientWalletRechargePayNow>
