<script>
import DataService from '../../../services/DataService';
import ClientWalletRechargePayNow from '../../../components/Modals/ClientWalletRechargePayNow.vue';
export default {
     props: {
        ClientId: {
            type: Number,
            required: true
        },
        parentId: {
            type: Number,
            required: true
        },
    },
    components: {
        ClientWalletRechargePayNow
    },
    data() {
        return {
            isLoading: false,
            Currency: '',
            TotalRecords: 0,
            RechargeAmount: null,
            HistoryData: [],
            isRechargeNow: false,
            isHidden: false,
            UtilityUsageData: [],
            TotalCousumption: null,
            TotalAmount: null,
            AverageCousumption: null,
            CurrentAmount: null,
            companyListData: [],
            selectedCompany: '',
            IsClientWalletRechargePayNow: false,
            thresholdCurrency: 0,
            ElectricData: [],
            WaterData: [],
            GasData: [],
            selectedCurrency: null,
            BaseCurrencyCode: null,
            UserId: '',
            AccountNumber:''
        };
    },
    async created() {
        this.Currency = this.GetUserInfo.Currency.replace(/[()]/g, '');
        await this.GetBaseCurrency();
        await this.GetBaseCurrencyCode();
        this.GetConfigurationValueByKey('Client', 'MinimumWalletBalance');
        this.GetCompanyDDlData();
        if(this.ClientId>0){
            this.selectedCompany=this.GetUserInfo.CompanyId;
        }else{
            this.UserId=this.GetUserInfo.ID;
        }
        this.GetClientCurrentBalence(this.selectedCompany);
        this.GetClientDashboardData(this.selectedCompany);
    },
    
    computed: {
        isRechargeAmountValid() {
            if (this.RechargeAmount === null || this.RechargeAmount === undefined || this.RechargeAmount === '') return false;
            const s = String(this.RechargeAmount);
            // format: up to 10 integer digits, optional up to 2 decimals
            const regex = /^\d{1,10}(?:\.\d{1,2})?$/;
            return regex.test(s) && Number(s) > 0;
        }
    },

    watch: {
        companyListData(newVal) {
            if (newVal && newVal.length > 0 && !this.selectedCompany) {
                this.selectedCompany = newVal[0].value;
                this.onCompanyChange();
            }
        }
    },
    methods: {
        async GetBaseCurrency() {
            var vm = this;
            var params = `configkey=Currency`;
            await DataService.GetBaseCurrency(params).then(response => {
                vm.selectedCurrency = response.data.CONFIG_DATA_VALUE;
            }).catch(err => {
                console.log(err);
            })
        },
        async GetBaseCurrencyCode() {
            var vm = this;
            const url = `CurrencyId=${vm.selectedCurrency}`;
            await DataService.GetBaseCurrencyCode(url).then((response) => {

                if (response.data) {

                    vm.BaseCurrencyCode = response.data[0].iso_code;

                }
                else {
                    vm.BaseCurrencyCode = null;
                }

            });
        },
        GetConfigurationValueByKey(moduleName, configKey) {
            var vm = this;
            var params = `moduleName=${moduleName}&configkey=${configKey}`;
            DataService.GetBaseCurrency(params).then((response) => {
                this.thresholdCurrency = response.data.CONFIG_DATA_VALUE;

            });
        },
        GetClientCurrentBalence(CompanyId) {
            var User_Client_id= this.ClientId>0 ? this.ClientId : this.UserId;
            const url = `Client_Company_Id=${CompanyId}&ClientId=${User_Client_id}`;
            DataService.GetClientCurrentBalence(url).then((response) => {
                if (response.data) {
                    this.CurrentAmount = response.data.current_Balance;
                    this.AccountNumber = response.data.crm_Account_No;
                }
                else {
                    this.CurrentAmount = '';
                    this.AccountNumber = '';
                }

            });
        },
        // Method to set the recharge amount based on button click
        setAmount(amount) {
            // store as string to keep formatting consistent with input handler
            this.RechargeAmount = String(amount);
        },

        // Enforce input rules: up to 10 integer digits and up to 2 decimal places
        onAmountInput(e) {
            
            let val = e.target.value || '';
            // Remove any characters except digits and dot
            val = val.replace(/[^0-9.]/g, '');
            // If more than one dot, keep only first
            const parts = val.split('.');
            let intPart = parts[0] || '';
            // Trim integer part to max 10 digits
            if (intPart.length > 10) intPart = intPart.slice(0, 10);
            let decPart = parts[1] || '';
            // Trim decimal part to max 2 digits
            if (decPart.length > 2) decPart = decPart.slice(0, 2);
            let newVal = intPart;
            if (decPart !== '') newVal = intPart + '.' + decPart;
            // Prevent multiple leading zeros like '00' -> keep single '0' unless decimal present
            if (/^0{2,}$/.test(newVal) && newVal.indexOf('.') === -1) {
                newVal = '0';
            }
            this.RechargeAmount = newVal;
        },

        // This method handles the recharge process
        WalletRecharge() {
            debugger
            // Validate RechargeAmount: must be positive, not zero, not empty and match format
            if (!this.isRechargeAmountValid) {
                this.ShowAlert('Please enter a valid amount greater than zero.', 'failure', true, this.$t('Alert'));
                return;
            }

            this.IsClientWalletRechargePayNow = true;

            //this.$router.push({ name: 'RechargePayNow', params: { Amount: this.RechargeAmount } });
        },
        clkRechargeNow() {
            debugger
            var User_Client_id= this.ClientId>0 ? this.ClientId : this.UserId;
            const url = `ClientId=${User_Client_id}`;
            DataService.GetClientWalletRechargeStatus(url).then((response) => {
                if (response.data) {
                    if(response.data == 'ACTIVE'){
                        this.isRechargeNow = true;
                    }
                    else if(response.data == 'INACTIVE'){
                        this.ShowAlert('Wallet has been inactive. Please contact the administrator.', 'warning', true, this.$t('Alert'));
                    }
                    else if(response.data == 'NOT EXIST'){
                        this.ShowAlert('Wallet access has been disabled. Please contact the administrator.', 'warning', true, this.$t('Alert'));
                    }
                }
            });
        },
        async GetClientDashboardData(CompanyId) {
            var vm = this;
            vm.isLoading = true;
             var User_Client_id= this.ClientId>0 ? this.ClientId : this.UserId;
            const url = `Client_Company_Id=${CompanyId}&ClientId=${User_Client_id}`;
            await DataService.GetClientDashboardDetail(url).then((response) => {
                debugger
                if (response.data != '') {
                    vm.ElectricData = response.data.filter(item => item.meter_type === "electric");
                    vm.WaterData = response.data.filter(item => item.meter_type === "water");
                    vm.GasData = response.data.filter(item => item.meter_type === "gas");
                }
                else {
                    vm.ElectricData = [];
                    vm.WaterData = [];
                    vm.GasData = [];

                }
                this.isLoading = false;
            })
        },
        // ViewHistory(UtilityName) {
        //     var vm = this;
        //     const url = `UtilityName=${UtilityName}`;
        //     DataService.GetUtilityUsageData(url).then((response) => {
        //         if (response.data) {
        //             let UsageData = JSON.parse(response.data.result);

        //             vm.UtilityUsageData = UsageData.Data;

        //             vm.TotalAmount = vm.UtilityUsageData[0].TotalAmount;
        //             vm.TotalCousumption = vm.UtilityUsageData[0].TotalConsumption;
        //             vm.AverageCousumption = vm.UtilityUsageData[0].AvgConsumption;

        //         }
        //         else {
        //             vm.UtilityUsageData = [];
        //             vm.TotalAmount = '';
        //             vm.TotalCousumption = '';
        //             vm.AverageCousumption = '';
        //         }

        //     });
        //     vm.isHidden = true;

        // },
        BackDashboard() {
            var vm = this;
            vm.isHidden = false;
            vm.UtilityUsageData = [];
            vm.TotalAmount = '';
            vm.TotalCousumption = '';
            vm.AverageCousumption = '';
        },
        closeModal() {
            this.isLoading = true;
            this.isRechargeNow = false;
            this.onCompanyChange();
            this.isLoading = false;
        },
        GetCompanyDDlData() {
            const vm = this;
            const url = `id=&moduleName=&type=GET_COMPANY_LIST&search=&refId=`;
            DataService.GetDDLData(url).then((response) => {
                if (response.data && response.data.DATA && response.data.DATA.length > 0) {
                    vm.companyListData = response.data.DATA;
                } else {
                    vm.companyListData = [];
                    vm.selectedCompany = "";
                }
            });
        },
        async onCompanyChange() {
            this.isLoading = true;
            var company = this.selectedCompany === "" ? this.GetUserInfo.CompanyId : this.selectedCompany;
            this.GetClientCurrentBalence(company);
            this.GetClientDashboardData(company);
            this.isLoading = false;
        },
        clkTransactionHistory() {
            var vm = this;
            if(this.ClientId>0){
                vm.$router.push({ name: 'TransactionWalletHistory', params: { id: this.selectedCompany === "" ? this.GetUserInfo.CompanyId : this.selectedCompany, client_id: this.ClientId ? this.ClientId :'',parentId:this.parentId? this.parentId:'' } });
            }else{
                vm.$router.push({ name: 'TransactionHistory', params: { id: this.selectedCompany === "" ? this.GetUserInfo.CompanyId : this.selectedCompany } });
            }
           
        },
        ViewHistory(utilityName, relatedInfoId) {
            var vm = this;
            if(this.ClientId>0){
                vm.$router.push({ name: 'UsageWalletHistory', params: { id: relatedInfoId,clientId: this.ClientId ? this.ClientId :'',parentId:this.parentId? this.parentId:''  } });
            }else{
                vm.$router.push({ name: 'UsageHistory', params: { id: relatedInfoId} });
            }
           
        }
    }
};
</script>

