<template>
  <section>
    <div class="my-popups">
      <loader :is-visible="isLoading" />

      <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content">

            <!-- Modal Header -->
            <div class="modal-header theme-primary partition-full d-flex justify-content-between align-items-center">
              <h5 class="modal-title">Payment Receipt</h5>
              <button type="button" class="close" @click="CloseClientWalletRechargeReceipt()">
                <em class="fa fa-times" aria-hidden="true"></em>
              </button>
            </div>

            <!-- Modal Body -->
            <div class="modal-body">
              <div class="invoice-container mt-3 border p-2" ref="printArea">

                <!-- Title -->
                <div class="text-center">
                  <h4 style="margin-top: 0; color: #000; font-family: Arial, Helvetica, sans-serif; font-size: 18px; background: #f2f2f2; padding: 10px;">
                    Wallet Recharge Receipt
                  </h4>
                </div>

                <!-- Company Info -->
                <div style="width: 100%; display: flex; justify-content: space-between; align-items: center; padding-bottom: 5px; border-bottom: 1px dashed rgb(217, 216, 216); margin-bottom: 10px;">
                  <img v-if="ReceiptData.company_logo" :src="ReceiptData.company_logo" style="width: 200px;" alt="Company Logo" />
                  <div style="color: rgb(0, 0, 0); font-family: Arial, Helvetica, sans-serif; font-size: 1em;">
                    <p style="font-weight:600; font-size:16px;">{{ ReceiptData.company_name || 'Company Name' }}</p>
                    <p>{{ ReceiptData.company_address || 'No Address Provided' }}</p>
                    <p>{{ (ReceiptData.company_zipcode || 'No Zip Code Provided') + ', ' + (ReceiptData.company_country || 'No Country Provided') }}</p>
                  </div>
                </div>

                <!-- Client Info -->
                <div class="header float-left w-100 p-2 mb-0">
                  <div class="float-left col-md-6 p-0" style="color: rgb(0, 0, 0); font-family: Arial, Helvetica, sans-serif; font-size: 1em;">
                    <p><strong>Client Name:</strong> {{ ReceiptData.FirstName || '---' }}</p>
                    <p><strong>Address:</strong> {{ address || '---' }}</p>
                  </div>
                  <div class="float-left col-md-6 p-0" style="color: rgb(0, 0, 0); font-family: Arial, Helvetica, sans-serif; font-size: 1em; display: flex; justify-content: flex-end;">
                    <p v-if="ReceiptData.bill_id">
                      <b>Invoice Number:</b> {{ ReceiptData.bill_id || '---' }}
                    </p>
                  </div>
                </div>

                <div style="clear: both;"></div>

                <!-- Payment Table -->
                <table class="table table-bordered mt-0 border mb-2 secondtable">
                  <tbody>
                    <tr>
                      <td style="font-weight: 600; color: #737373; background: #f9f9f9;">Payment Method</td>
                      <td style="font-weight: 600; color: #737373; background: #f9f9f9;">Transaction ID</td>
                      <td style="font-weight: 600; color: #737373; background: #f9f9f9;">Payment Date</td>
                      <td style="font-weight: 600; color: #737373; background: #f9f9f9;">Status</td>
                    </tr>
                    <tr>
                      <td>{{ ReceiptData.payment_gateway_name || '---' }}</td>
                      <td>{{ ReceiptData.TransactionID || '---' }}</td>
                      <td>{{ ReceiptData.created_at ? $options.filters.formatDateTime(ReceiptData.created_at) : '---' }}</td>
                      <td :style="{ color: isPaymentSuccess ? 'green' : 'red' }">{{ ReceiptData.Message || '---' }}</td>
                    </tr>
                  </tbody>
                </table>

                <!-- Recharge Amount Table -->
                <table class="table table-bordered mt-2 text-right border float-right secondtable" style="width:400px;">
                  <tbody>
                    <tr>
                      <td style="font-weight: 600; color: #737373; font-family: Arial, Helvetica, sans-serif; font-size: 1em; background: rgb(249 249 249);">
                        Recharge Amount
                      </td>
                      <td style="text-align:right; color: #000; font-family: Arial, Helvetica, sans-serif; font-size: 1.20em; font-weight:600;">
                        Rs {{ parseFloat(ReceiptData.Amount || 0).toFixed(2) }}
                      </td>
                    </tr>
                  </tbody>
                </table>

                <div style="clear: both;"></div>
                <p :class="['mt-1', 'fw-bold', isPaymentSuccess ? 'alert alert-success text-center' : 'alert alert-danger text-center']">
                  <span v-if="isPaymentSuccess">
                    Thank you! Your transaction has been processed successfully.
                  </span>
                  <span v-else>
                    Your transaction has failed. Please try again.
                  </span>
                </p>

                <!-- Footer Buttons -->
                <div class="text-right mt-3 no-print">
                  <button class="btn btn-success mr-2" @click="downloadReceipt">
                    <em class="fa fa-print"></em> Download Receipt
                  </button>
                  <button class="btn btn-danger" @click="goBack">
                    <em class="fa fa-close"></em> Back to Wallet
                  </button>
                </div>
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import DataService from "../../services/DataService";

export default {
  props: {
    TransactionId: {
      type: Number,
      required: true,
    }
  },

  data() {
    return {
      isLoading: false,
      RechargeReceipt: null,
      ReceiptData: {},
      ResponceMsg: null,
      CurrencyCode: '',
      isPrinting: false,
      address: ''
    };
  },

  computed: {
    isPaymentSuccess() {
      if (!this.ReceiptData.Message) return false;

      const normalizedMsg = this.normalizeMessage(this.ReceiptData.Message);
      return normalizedMsg === 'success' || normalizedMsg.includes('approved');
    }
  },

  created: async function () {
    var vm = this;
    vm.CurrencyCode = vm.GetUserInfo.Currency.replace(/[()]/g, '');

    try {
      if (typeof vm.TransactionId === 'string') {
        const parsedData = JSON.parse(vm.TransactionId);
        vm.RechargeReceipt = parsedData.TransactionId;
      } else {
        vm.RechargeReceipt = vm.TransactionId;
      }
    } catch (error) {
      console.error('Error parsing TransactionId:', error);
      vm.RechargeReceipt = vm.TransactionId;
    }

    vm.GetClientReceiptData();
  },

  methods: {
    normalizeMessage(msg) {
      // Converts to lowercase, trims spaces, removes trailing punctuation
      return (msg || '').toLowerCase().trim().replace(/[.\s]+$/g, '');
    },

    CloseClientWalletRechargeReceipt() {
      this.$parent.IsRechargeReceiptShow = false;
    },

    downloadReceipt() {
      this.isPrinting = true;
      setTimeout(() => {
        window.print();
        this.isPrinting = false;
      }, 100);
    },

    goBack() {
      const normalizedMsg = this.normalizeMessage(this.ResponceMsg);
      if (normalizedMsg === 'success' || normalizedMsg.includes('approved')) {
        // Successful payment flow
        this.$parent.$parent.onCompanyChange();
      }

      // Reset parent states in both success & failure
      this.$parent.$parent.RechargeAmount = null;
      this.$parent.$parent.isRechargeNow = false;
      this.$parent.$parent.IsClientWalletRechargePayNow = false;
    },

    GetClientReceiptData() {
      let vm = this;
      vm.isLoading = true;
      const url = `TransactionId=${vm.RechargeReceipt}`;
      const safe = (v) => v !== null && v !== undefined ? v : '';

      DataService.GetClientReceiptData(url)
        .then((response) => {
          if (!response.data) {
            vm.ReceiptData = {};
            return;
          }

          let base = response.data;
          let parsed = {};

          try {
            if (base.response_Json) parsed = JSON.parse(base.response_Json);
          } catch (err) {
            console.error("JSON parse error:", err);
          }

          vm.ReceiptData = {
            ...base,
            ...parsed,
            company_logo: safe(base.company_Logo),
            company_name: safe(base.company_Name),
            company_address: safe(base.company_Address),
            company_city: safe(base.company_City),
            company_state: safe(base.company_State),
            company_country: safe(base.company_Country),
            company_zipcode: safe(base.company_ZipCode),
            user_address_1: safe(parsed.user_address_1),
            user_address_2: safe(parsed.user_address_2),
            user_city: safe(parsed.user_city),
            user_state: safe(parsed.user_state),
            user_zipcode: safe(parsed.user_zipcode),
            user_country: safe(parsed.user_country),
            payment_gateway_name: safe(parsed.payment_gateway_name || base.payment_gateway_name || 'N/A')
          };

          vm.ReceiptData.created_at = parsed.created_at || base.created_At || null;
          vm.ReceiptData.TransactionID = vm.ReceiptData.TransactionID || vm.RechargeReceipt;
          vm.ReceiptData.FirstName = vm.ReceiptData.FirstName || vm.GetUserInfo?.Name || '';
          vm.ReceiptData.Amount = vm.ReceiptData.Amount || vm.ReceiptData.field3 || 0;
          vm.ReceiptData.Message = safe(vm.ReceiptData.Message || vm.ReceiptData.status || 'N/A');

          vm.address = vm.formatFullAddress(vm.ReceiptData) || 'N/A';
          vm.ResponceMsg = vm.ReceiptData.Message;
        })
        .catch((error) => {
          console.error("Error fetching receipt data:", error);
          vm.ReceiptData = {};
        })
        .finally(() => {
          vm.isLoading = false;
        });
    },

    formatFullAddress(data) {
      return [
        data.user_address_1,
        data.user_address_2,
        data.user_city,
        data.user_state,
        data.user_zipcode,
        data.user_country
      ]
        .filter(v => v && v.trim() !== '')
        .join(', ');
    }
  },
};
</script>

<style scoped>
.receipt-section {
    background: #f7f9fc;
}

table.secondtable {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

table.secondtable th,
table.secondtable td {
    border: 1px solid #ddd!important;
    padding: 10px;
    text-align: left;
}

table.secondtable th {
    background-color: #f4f4f4;
}
</style>
